body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#212529 !important;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#App {
  box-sizing: border-box;
  box-sizing: inherit;
  text-align: center;
  background-image: url(/portfolio/static/media/bg.e46e364f.jpg);
  background-repeat: no-repeat;
  background-color:#212529;
  border: 0;

}




.navbar{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	text-align: start  !important;
}

.icons{
	text-align: start !important;
}
.nav-link :hover{
    color:#18bfef !important;
}

.fa-twitter{
	color: #1da1f2;
}

.fa-envelope{
	color: #fd1111de;
}

.fa-linkedin-in{
	color: #0077b5;
}

.fa-github{
	color: #6cc644;
}
.container{
    margin-top: 220px;
	background-color: white;
    vertical-align: baseline;
    font: inherit;
    box-sizing: inherit;
    text-align: center;
}
.card1{
    border: 0ch;
    background-color: white;
}

.card2{
    max-width: 540px;
    border: 0ch;
    margin-top: 100px;
}
.card-title{
    margin-top: 70px;
}

.card-img-top{
  width: 50%;
  height: 250px;
  margin-top: 50px;
}
.card-img{
    height: 100%;
}
h1 a{
    color: black;
    text-transform: uppercase;
    font-weight: 900;
}
h3 a{
    color: black;
    text-transform: uppercase;
    font-weight: 900;
}

.contact{
    background-color:#212529;
	color: white;
	margin-top: 220px !important;
	margin-bottom: 100px;
}
.info{
	margin-top: 30px;
	display: block;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	text-align: start;
}
.col{
	margin: 15px;
}


.myImg{
  border-radius: 50%;
  height: 250px;
  float: inherit;
  margin-top: 150px;
}

.p1{
    color: white;
    margin: 0px 50px 0px 50px;
    vertical-align: baseline;
    font: inherit;
    font-size: 1.3rem;
    box-sizing: inherit;
   /*  margin-inline-start: 0px;
    margin-inline-end: 0px; */
}
.myName{
    color: white;
    font-family: 'Baloo Chettan', cursive;
    font-weight: 1050;
    line-height: 1.8;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    margin: 0 0 1rem 0;
    font-size: 4rem;
}
.arrow{
    color: white !important; 
    margin-top: 150px;
    border: 0ch;
}
.arrow:hover{
    color:#18bfef;
    text-decoration: none;
    border: #18bfef
}
a:hover{
    color:#18bfef !important;
    text-decoration: none;
    border: #18bfef
}
a{
    color: white;
    border: 0ch;
}
li {
    list-style: none;
}


